nav {
  padding: 0.125rem;
  display: flex;
}
.nav-container {
  flex: 1;
  display: flex;
  justify-content: space-between;
  height: 100%;
  border-bottom: 1px solid #b0b0b0;
}
.nav-center {
  /* flex: 0 1 150px; */
  flex: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 1.5rem;
}
.nav-center > h2 {
  color: #fff;
  text-transform: capitalize;
  font-size: 1.8rem;
}
.nav-number-center {
  flex: 0 1 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: yellow;
  padding: 0.5rem;
}

.nav-number-img {
  color: black;
  font-size: 1.2rem;
}
.nav-number-table {
  color: black;
  font-size: 1.2rem;
}
