* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}
main {
  width: 100vw;
  height: 100vh;
  margin: 0 auto;
  background-color: #000;
  /* border: 1px solid #b0b0b0; */
  /* border-radius: 0.25rem; */
}
.main-container {
  /* width: 100vw; */
  /* height: 100vh; */
  width: 100%;
  height: 100%;
  margin: 0 auto;
  border: 1px solid #b0b0b0;
  border-radius: 0.25rem;
  /* display: flex; */
  /* justify-content: center; */
  /* align-items: center; */
  /* margin: 0 auto; */
  /* padding: 0.25rem; */
  /* border: 1px solid red; */
  /* position: relative; */
}

.main-center {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100vh;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 80px 1fr 80px;
  position: relative;
}
nav {
  grid-column: 1 / 4;
}
aside {
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  /* border: 1px solid blue; */
}
section {
  grid-column: 2 / 4;
  grid-row: 2 / 3;
  /* border: 1px solid red; */
}
footer {
  grid-column: 1 / 4;
  grid-row: 3 / 4;
  /* border: 1px solid rebeccapurple; */
}

@media screen and (max-width: 479px) {
  main {
    max-width: 100%;
    max-height: 100vh;
  }
  .main-center {
    grid-template-columns: 1fr;
  }
}
@media screen and (min-width: 480px) and (max-width: 767px) {
  main {
    max-width: 480px;
    max-height: 700px;
  }

  .main-center {
    grid-template-columns: 1fr 2fr;
  }
  nav {
    grid-column: 1 / 3;
    /* border: 1px solid black; */
  }
  aside {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  section {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  footer {
    grid-column: 1 / 3;
    grid-row: 3 / 4;
    /* border: 1px solid black; */
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  main {
    max-width: 768px;
    max-height: 800px;
  }

  .main-center {
    grid-template-columns: 1fr 3fr;
    /* border: 1px solid red; */
  }
  nav {
    grid-column: 1 / 4;
    /* border: 1px solid black; */
  }
  aside {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  section {
    grid-column: 2 / 4;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  footer {
    grid-column: 1 / 4;
    grid-row: 3 / 4;
    /* border: 1px solid black; */
  }
}

@media screen and (min-width: 1024px) and (max-width: 1279px) {
  main {
    max-width: 1024px;
    max-height: 850px;
  }
  .main-center {
    grid-template-columns: 1fr 4fr;
  }
  nav {
    grid-column: 1 / 5;
    /* border: 1px solid black; */
  }
  aside {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  section {
    grid-column: 2 / 5;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  footer {
    grid-column: 1 / 5;
    grid-row: 3 / 4;
    /* border: 1px solid black; */
  }
}
@media screen and (min-width: 1280px) and (max-width: 1439px) {
  main {
    max-width: 1280px;
    max-height: 900px;
  }
  .main-center {
    grid-template-columns: 1fr 4fr;
  }
  nav {
    grid-column: 1 / 5;
    /* border: 1px solid black; */
  }
  aside {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  section {
    grid-column: 2 / 5;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  footer {
    grid-column: 1 / 5;
    grid-row: 3 / 4;
    /* border: 1px solid black; */
  }
}

@media screen and (min-width: 1440px) {
  .main-container {
    max-width: 1440px;
    max-height: 1000px;
  }
  .main-center {
    grid-template-columns: 1fr 5fr;
  }
  nav {
    grid-column: 1 / 6;
    /* border: 1px solid black; */
  }
  aside {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  section {
    grid-column: 2 / 6;
    grid-row: 2 / 3;
    /* border: 1px solid black; */
  }
  footer {
    grid-column: 1 / 6;
    grid-row: 3 / 4;
    /* border: 1px solid black; */
  }
}
