.cart {
  display: none;
}

.show-cart {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: transparent;
  transition: all 0.3s linear;
}
.cart-container {
  position: absolute;
  top: 0;
  right: 0;
  width: 45%;
  height: 100%;
  background-color: #4d4d4d;
  border: 1px solid rgba(255, 255, 255, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.item-cart-title {
  flex: 0 80px;
  padding: 0.125rem 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  margin-bottom: 0.5rem;
}

.item-cart-close {
  /* border: 1px solid red; */
}

.cart-btns {
  /* flex: 0 80px; */
  /* display: flex; */
  display: flex;
  padding: 0.125rem;
  /* border: 1px solid red; */
}
.cart-left,
.cart-right {
  width: 100px;
  height: 40px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: bold;
}
.cart-left {
  flex: 0 35%;
}
.cart-right {
  flex: 1;
}
