.item-article {
  width: 100%;
  height: 100%;
  padding: 0.125rem;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.25rem;
  transition: all 0.3s linear;
  cursor: pointer;
}
.item-article:hover {
  transform: translateY(-1px);
  box-shadow: 0 5px 15px rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 1);
  background-color: rgb(19, 19, 19);
}
.item-container {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 50px 30px;
  /* border: 1px solid red; */
}

.item-img {
  position: relative;
}
.single-item-img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: fill;
  border-radius: 0.25rem;
}
.item-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.item-info p {
  color: #fff;
  padding: 0.125rem;
}
.item-btn {
  width: 100%;
  height: 100%;
}
.addItem-btn {
  display: block;
  width: 100%;
  height: 100%;
  padding: 0.125rem;
  color: #fff;
  border-radius: 0.25rem;
  background-color: transparent;
  border-color: transparent;
  cursor: pointer;
  background-color: #007bff;
  transition: all 0.3s linear;
}
.addItem-btn:hover {
  background-color: #007bff;
  color: #fff;
}
