.category-btns::-webkit-scrollbar {
  display: none;
}

.category-btns {
  -ms-overflow-style: none; /* 인터넷 익스플로러 */
  scrollbar-width: none; /* 파이어폭스 */
}

.category {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0.125rem;
  overflow: hidden;
}

.sidebar-container {
  flex: 1;

  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;

  /* border: 1px solid red; */
}
.category-title {
  flex: 0 0 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  margin-bottom: 0.25rem;
}
.category-btns {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
  /* border: 1px solid red; */
}
.filter-btn {
  flex: 0 0 50px;
  margin-bottom: 0.25rem;
  cursor: pointer;
  color: #fff;
  border-radius: 0.25rem;
  font-size: 1rem;
  font-weight: bold;
  background-color: darkorange;
  letter-spacing: 0.25rem;
}
.active-category,
.filter-btn:hover {
  background-color: yellow;
  color: #000;
}

