.addItem-container {
  height: 100%;
  display: flex;
  align-items: center;
  /* justify-content: flex-end; */
  justify-content: space-between;
  padding-right: 1rem;
  padding-left: 1rem;
  padding: 0 1rem;
  border-top: 1px solid #b0b0b0;
}
.addItem-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.call-btn,
.left-btn,
.right-btn {
  margin: 0 0.5rem;
  width: 130px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* font-size: 0.9rem; */
  font-size: 1rem;
  font-weight: bold;
  background-color: #fff;
  color: #000;
  border-radius: 0.25rem;
  cursor: pointer;
}
.call-btn .icon,
.left-btn .icon,
.right-btn .icon {
  font-size: 1.4rem;
  margin-right: 8px;
  margin-right: 12px;
  /* border: 1px solid red; */
}

.cart-order {
  position: relative;
}
.cart-order .cart-amount {
  position: absolute;
  top: 1px;
  left: 34px;
  /* z-index: 1; */
  /* border: 1px solid red; */
}

.shake {
  background-color: #f44336;
  color: #fff;
  animation: shake 0.5s linear;
  animation-iteration-count: 4;
}
@keyframes shake {
  0%,
  100% {
    transform: translateX(0);
  }
  20%,
  60% {
    transform: translateX(-2px);
  }
  40%,
  80% {
    transform: translateX(2px);
  }
}
