.modal {
  display: none;
}
.show-modal {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background-color: transparent; */
  background-color: rgba(0, 0, 0, 0.5);
}
.modal-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  height: 70%;
  /* padding: 0.5rem; */
  display: flex;
  flex-direction: column;
  /* background-color: #fff; */
  background-color: #4d4d4d;
}
.modal-container h2 {
  flex: 0 0 50px;
  display: flex;
  align-items: center;
  margin-bottom: 0.25rem;
  background-color: #000;
  color: yellow;
  margin: 0.125rem;
  padding-left: 0.8rem;
  /* border: 1px solid red; */
}
.modal-center {
  flex: 1;
  display: flex;
  padding: 0.125rem;
  margin-bottom: 0.25rem;
  margin: 0.125rem 0.125rem 0.25rem;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.5);
  /* border: 1px solid blue; */
}
.modal-items {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  /* height: 100%; */
  padding: 0.125rem;
  /* border: 1px solid red; */
}
.modal-items-center {
  flex: 1;
  display: grid;
  grid-template-columns: 0.6fr 1fr;
  gap: 0.25rem;
  overflow: hidden;
  margin-bottom: 0.25rem;
  /* border: 1px solid red; */
}
.modal-menuItem {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.25rem;
  background-color: #b0b0b0;
}
.modal-menuItem img {
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
}
.modal-info {
  display: flex;
  flex-direction: column;
  border-radius: 0.25rem;
  overflow: auto;
  border: 1px solid rgba(255, 255, 255, 0.5);
}
.modal-items-title {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.25rem;
  padding: 0.25rem;
  /* border: 1px solid red; */
}
.modal-items-title h3 {
  padding: 0.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}
.modal-items-title span {
  color: #fff;
}
.drink-type {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.125rem;
  margin-bottom: 0.25rem;
  /* border: 1px solid red; */
}
.drink-type .hot-type,
.drink-type .iced-type {
  margin: 0.5rem;
  width: 60px;
  height: 100%;
  border-radius: 0.5rem;
  background-color: transparent;
  cursor: pointer;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  letter-spacing: 0.05rem;
  color: #b0b0b0;
}
.hot-type {
  border: 2px solid rgba(255, 0, 0, 0.5);
  border: 2px solid #b0b0b0;
}
.iced-type {
  border: 2px solid rgba(0, 0, 255, 0.5);
  border: 2px solid #b0b0b0;
}
.hot-type:hover {
  color: red;
  border: 2px solid rgba(255, 0, 0, 0.8);
}
.iced-type:hover {
  color: blue;
  border: 2px solid rgba(0, 0, 255, 0.8);
}
.active-hot.hot-type {
  color: red;
  border: 2px solid rgba(255, 0, 0, 0.8);
  background-color: #b0b0b0;
}
.active-iced.iced-type {
  color: blue;
  border: 2px solid rgba(0, 0, 255, 0.8);
  background-color: #b0b0b0;
}
.items-info {
  display: flex;
  flex-direction: column;
  padding: 0.125rem 0.25rem 0.25rem;
}
.coffee-row {
  display: flex;
  flex-direction: column;
  margin-bottom: 0.25rem;
  /* border: 1px solid red; */
}
.coffee-row h4,
.coffee-row p {
  text-align: center;
  font-size: 1rem;
  color: #fff;
  margin-bottom: 0.2rem;
}
.coffee-row p {
  font-size: 0.8rem;
}
.btn-row {
  flex: 0 0 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}
.btn-row .coffee-size {
  flex: 1;
  margin: 0 2px;
  font-size: 0.9rem;

  padding: 0.25rem;
  background-color: transparent;
  border-color: transparent;
  text-transform: capitalize;
  font-weight: bold;
  cursor: pointer;
  color: #b0b0b0;
  /* border: 1px solid red; */
}
.btn-row .active-size {
  color: #fff;
  /* background-color: red; */
  /* border: 1px solid red; */
}
.btn-row .coffee-size:hover {
  color: #fff;
}
.btn-row button.minus,
.btn-row button.plus {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  margin: 0 1.5rem;
  background-color: transparent;
  border-color: transparent;
  width: 30px;
  height: 24px;
  height: 100%;
  color: #b0b0b0;
  cursor: pointer;
  /* border: 1px solid red; */
}
.btn-row svg {
  /* border: 1px solid red; */
}
.btn-row button.minus:hover,
.btn-row button.plus:hover {
  color: #fff;
  border: 1px solid #fff;
  border-radius: 0.5rem;
}

.btn-row span {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  color: #fff;
  width: 30px;
  /* height: 24px; */
  height: 100%;
  /* border: 1px solid red; */
}
