.item-total {
  flex: 0 0 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  color: #fff;
  font-weight: bold;
  letter-spacing: 0.07rem;
}
.order-btn {
  flex: 0 0 40px;
  display: flex;
  margin: 0 0.125rem 0.25rem;
}
.modal-btn-left {
  flex: 0 0 30%;
  margin-right: 0.1rem;
  background-color: gray;
  border-color: transparent;
  font-size: 1rem;
  cursor: pointer;
  font-weight: bold;
  border-radius: 0.25rem;
}
.modal-btn-left:hover {
  background-color: #000;
  color: #fff;
}
.modal-btn-right {
  flex: 1;
  margin-left: 0.1rem;
  background-color: yellow;
  border-color: transparent;
  color: #000;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 0.25rem;
  cursor: pointer;
}
.modal-btn-right:hover {
  background-color: #000;
  color: yellow;
}
